import React, { useEffect, useState } from "react";
import Post from "./Post";
import { useSelector, useDispatch } from "react-redux";
import { useCollection } from '../hooks/useCollection';
import { setUserList } from "../store/searchSlice";

function Feeds() {
    const postList = useSelector((state) => state.feeds); // ë¡œì»¬ ìƒ˜í”Œ ë°ì´í„°
    const {documents,error} = useCollection("FeedData"); // ì„œë²„ ë¦¬ì–¼ ë°ì´í„°
    let [fade, setFade] = useState('') // Animation Style State
    let dispatch = useDispatch()

    useEffect(()=>{
        console.log('Mock Data',postList)
    }, [])

    useEffect(()=>{
        console.log('Server Data',documents)
        setFade(documents!= null && documents.length > 0? 'transition-end': '') // ì• ë‹ˆë©”ì´ì…˜ íš¨ê³¼

        // ì „ì²´ ê¸€ì˜ ì‚¬ìš©ì ì •ë³´(UID , displayName)ë¥¼ ë°›ì•„ì„œ redux storeì— ë„£ê¸°
        let users = [] // ì „ì²´ ìœ ì €
        // ìˆì„ ë•Œë§Œ ëŒë¦½ì‹œë‹¤?
        documents?.map((a,i)=>{
            users.push({UID : a.UID, Name : a.displayName})
        });
        let uniqUsers = [] // ì¤‘ë³µ X ìœ ì €
        // ì¤‘ë³µ ì œê±°
        uniqUsers = users.reduce((acc, cur)=>{
            if (acc.findIndex(({ UID }) => UID === cur.UID) === -1) {
                acc.push(cur)
            }
            return acc;
        }, []);
        console.log("ìœ ì € ëª©ë¡ì“°", uniqUsers)
        dispatch(setUserList(uniqUsers))
    }, [documents])

    return (
        <>
            <div className={`all-feeds transition-start ${fade}`}>
                {/* ë¦¬ì–¼ ë°ì´í„° ì¶œë ¥ */}
                {
                    documents ? 
                    documents.map((a,i) => {
                        let post = {...a}
                        return (<Post key={i+postList.length} post={post}/>) 
                    })
                    : null
                }

                {/* ìƒ˜í”Œ ë°ì´í„° ì¶œë ¥ */}
                {
                    postList.map((a,i) => {
                        return (<Post key={i+1} post={a} />)
                    })
                }
            </div>
        </>
    );
}

export default Feeds;